<template>
  <div v-show="flag == 1" v-bind:class="data.num % 2 == 1 ? 'contentbox' : 'contentbox1'">
    <div
      v-show="likeFlag != 1"
      v-if="this.openid == data.uid"
      v-on:click="drop"
      class="drop"
    >
      我要下墙
    </div>
    <p class="actor-title">
      {{ data.episode }}.{{ data.num }}{{ data.num % 2 == 0 ? "男神" : "女神" }}
    </p>
    <div class="pic-content">
      <img
        class="actor-pic"
        v-for="(item, index) in data.image"
        :key="index"
        :src="item"
      />
    </div>
    <div class="main-info">
      <div class="nickName">昵称:{{ data.name }}</div>
      <div class="school">院校:{{ data.school }}</div>
    </div>
    <div class="sub-info">
      <div class="age">年龄:{{ data.age }}</div>
      <div class="height">身高:{{ data.height }}</div>
      <div class="constellation">星座:{{ data.star }}</div>
      <div class="place">所在地:{{ data.location }}</div>
    </div>
    <div class="about-me">关于我:{{ data.introduction }}</div>
    <div class="target">我喜欢的TA:{{ data.goal }}</div>
    <div v-show="likeFlag != 1" v-if="data.isConnect == true" class="connect">
      联系方式:{{ data.connect }}
    </div>
    <div v-on:click="like" class="like">
      <img
        v-bind:class="isLiked == true ? 'heart-anim' : 'heart'"
        src="../assets/爱心.svg"
      />
      <span
        v-show="data.isLiked || isLiked"
        v-bind:class="isLiked == true ? 'likes' : 'likes'"
        >{{ data.likes }}</span
      >
    </div>
    <button v-show="likeFlag != 1" v-on:click="getInfo" class="get-info">
      获取联系方式
    </button>
    <p v-on:click="feedback" class="feedback">反馈</p>
    <div
      v-show="showFeedback"
      class="layer"
      @scroll.stop.prevent
      @touchmove.stop.prevent
    ></div>
    <div
      v-show="showDrop"
      class="layer"
      @scroll.stop.prevent
      @touchmove.stop.prevent
    ></div>
    <div
      v-show="showFeedback"
      class="feedback-log"
      @scroll.stop.prevent
      @touchmove.stop.prevent
    >
      <textarea
        rows="5"
        cols="30"
        class="question"
        v-model="question"
        placeholder="反馈问题"
      />
      <input
        class="feed-connect"
        v-model="feedConnect"
        type="text"
        placeholder="联系方式(QQ或邮箱等)"
      />
      <div class="button-content">
        <button v-on:click="submit">{{ btnWord }}</button>
        <button v-on:click="cancel">取消</button>
      </div>
    </div>
    <div
      v-show="showDrop"
      class="feedback-log"
      @scroll.stop.prevent
      @touchmove.stop.prevent
    >
      <input
        class="feed-connect"
        v-model="mail"
        type="text"
        placeholder="上墙时填写的邮箱"
      />
      <div class="button-content">
        <button v-on:click="submitDrop">下墙</button>
        <button v-on:click="cancel">取消</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ContentBox",
  props: ["data", "openid", "flag", "likeFlag"], //flag控制是否显示此组件,likeFlag控制点赞是否单次
  data: function () {
    return {
      btnWord: "提交",
      question: "",
      feedConnect: "",
      showFeedback: false,
      isLiked: false,
      mail: "",
      showDrop: false,
    };
  },
  mounted() {},
  methods: {
    drop: function () {
      this.showDrop = true;
    },
    like: function () {
      //先判断likeFlag,0为普通点赞,1为互选点赞
      this.$data.isLiked = true;
      if (this.likeFlag == 0) {
        this.axios.post("http://www.scgxtd.cn/public/user/message/like", {
          ID: this.openid,
          actorUid: this.data.uid,
        });
      } else if (this.likeFlag == 1) {
        this.axios
          .post("http://www.scgxtd.cn/public/user/message/like", {
            openid: this.openid,
            actorid: this.data.uid,
          })
          .then((res) => {
            if (res.data.code == -1) {
              alert("点赞失败");
            }
          });
      }
      console.log(this.openid);
      if (this.openid != "" || this.openid != null)
        this.axios.post("url", {
          openid: this.openid,
          actorUid: this.data.uid,
        });
    },
    feedback: function () {
      this.$data.showFeedback = true;
    },
    submit: function () {
      this.btnWord = "正在提交";
      this.axios
        .post("http://www.scgxtd.cn/public/user/suggestion", {
          question: this.question,
          connect: this.feedConnect,
        })
        .then((res) => {
          this.btnWord = "提交";
          this.$data.showFeedback = false;
          if (res.data.code == 0) {
            alert("反馈成功");
          } else if (res.data.code == -1) {
            alert("反馈失败" + res.data.msg);
          }
        });
      console.log(this.$data.question);
      console.log(this.$data.feedConnect);
      this.$data.showFeedback = false;
    },
    submitDrop: function () {
      this.axios
        .post("/http://www.scgxtd.cn/public/user/message/shower", {
          openid: this.openid,
          email: this.mail,
        })
        .then((res) => {
          if (res.data.code == 0) {
            alert("下墙成功");
          } else if (res.data.code == -1) {
            alert("下墙失败" + res.data.msg);
          }
        });
    },
    cancel: function () {
      this.$data.showFeedback = false;
      this.showDrop = false;
    },
    getInfo: function () {
      // let p = this.$qs.stringify({

      // });

      this.axios
        .post("http://www.scgxtd.cn/public/index/wxpay/getjsparam", {
          openid: "ontNP6DPWBgc1bKdDlH3MUwkSzfY",
          actorid: "123",
        })
        .then((res) => {
          if (typeof WeixinJSBridge == "undefined") {
            if (document.addEventListener) {
              document.addEventListener("WeixinJSBridgeReady", this.onBridgeReady, false);
            } else if (document.attachEvent) {
              document.attachEvent("WeixinJSBridgeReady", this.onBridgeReady);
              document.attachEvent("onWeixinJSBridgeReady", this.onBridgeReady);
            }
          } else {
            this.onBridgeReady(res);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    onBridgeReady(params) {
      // let myParam = JSON.stringify(params);
      // eslint-disable-next-line no-undef
      WeixinJSBridge.invoke(
        "getBrandWCPayRequest",
        {
          appId: params.data.appId, //公众号名称，由商户传入
          timeStamp: params.data.timeStamp, //时间戳，自1970年以来的秒数
          nonceStr: params.data.nonceStr, //随机串
          package: params.data.package,
          signType: "MD5", //微信签名方式：
          paySign: params.data.paySign, //微信签名
        },
        function (res) {
          if (res.err_msg == "get_brand_wcpay_request:ok") {
            alert("支付成功！");
          }
        }
      );
    },
  },
};
</script>
<style scoped>
@keyframes pop {
  0% {
    margin: 0 auto;
    position: relative;
    width: 50px;
    height: 50px;
  }
  50% {
    margin: 0 auto;
    position: relative;
    width: 70px;
    height: 60px;
  }
  100% {
    margin: 0 auto;
    position: relative;
    width: 50px;
    height: 50px;
  }
}
@-webkit-keyframes pop {
  0% {
    margin: 0 auto;
    position: relative;
    width: 50px;
    height: 50px;
  }
  50% {
    margin: 0 auto;
    position: relative;
    width: 70px;
    height: 60px;
  }
  100% {
    margin: 0 auto;
    position: relative;
    width: 50px;
    height: 50px;
  }
}
@keyframes poptext {
  0% {
    font-size: 16px;
  }
  50% {
    font-size: 18px;
  }
  100% {
    font-size: 16px;
  }
}
@-webkit-keyframes poptext {
  0% {
    font-size: 16px;
  }
  50% {
    font-size: 18px;
  }
  100% {
    font-size: 16px;
  }
}
.contentbox {
  z-index: 0;
  border-style: solid;
  border-radius: 10px;
  padding: 10px;
  margin: 0 auto;
  margin-bottom: 15px;
  color: rgb(255, 118, 141);
}
.contentbox1 {
  z-index: 0;
  border-style: solid;
  border-radius: 10px;
  padding: 10px;
  margin: 0 auto;
  margin-bottom: 15px;
  color: rgb(205, 118, 255);
}
.actor-title {
  text-align: left;
  width: 100%;
}
.pic-content {
  border-style: solid;
  border-width: 2px;
  border-radius: 10px;
  width: 95%;
  height: 200px;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  margin: 0 auto;
}
.actor-pic {
  max-width: 100%;
  max-height: 200px;
  scale: 1;
}
.main-info {
  margin-top: 20px;
  display: grid;
  grid-template-columns: 120px 230px;
}
.nickName {
  text-align: left;
}
.school {
  text-align: left;
}
.sub-info {
  display: grid;
  grid-template-columns: 120px 150px;
  font-size: 14px;
  margin-bottom: 10px;
}
.age {
  text-align: left;
}
.height {
  text-align: left;
}
.constellation {
  text-align: left;
}
.place {
  text-align: left;
}
.like {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 70px;
  height: 70px;
  position: relative;
}
.likes {
  color: white;
  position: absolute;
  left: 30%;
  top: 33%;
}
.likes-anim {
  animation: poptext 0.6s;
  color: white;
  position: absolute;
  left: 30%;
  top: 33%;
}
.like-anim {
  animation: pop 0.3s;
  margin: 0 auto;
  position: relative;
  width: 50px;
  height: 50px;
}
.heart {
  margin: 0 auto;
  width: 50px;
  height: 50px;
}
.heart-anim {
  animation: pop 0.5s;
  margin: 0 auto;
  width: 50px;
  height: 50px;
}
.about-me {
  text-align: left;
}
.target {
  text-align: left;
}
.get-info {
  background-color: rgba(250, 69, 138, 0.664);
  color: white;
  width: 70%;
  height: 35px;
  border-style: none;
  border-radius: 10px;
}
.feedback {
  color: rgb(158, 158, 158);
  display: inline;
  float: right;
  position: relative;
}
.layer {
  z-index: 150;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.6;
}
.feedback-log {
  z-index: 200;
  opacity: 1;
  position: fixed;
  width: 99%;
  top: 250px;
  left: 0;
  max-width: 470px;
  background-color: white;
  height: 200px;
  border-style: solid;
  border-width: 2px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.question {
  margin-top: 20px;
  width: 75%;
}
.feed-connect {
  width: 75%;
  height: 25px;
}
.button-content {
  margin: 0 auto;
  margin-top: 20px;
  width: 200px;
  display: flex;
  justify-content: space-around;
}
.button-content > button {
  width: 70px;
  height: 40px;
  border-radius: 10px;
  border-color: white;
  border-style: solid;
  background-color: pink;
  color: white;
}
.connect {
  text-align: left;
}
.drop {
  text-align: right;
}
</style>
